<template>
<div class="container">
  <div class="head">
    <div class="user">
      <div class="user-head">
        <h1>{{h1 | tolowercase}} </h1>
      </div>
      <div class="proqress">
        <div class="bar"  v-color.delay.flash="{maincolor:'white',lastcolor:'red',delay:500}" :style="{width:user+'%',backgroundColor: '#11ba'+(user-1)}"><b>{{user}}%</b></div>
      </div>
    </div>
    <div class="player" v-color:background.delay.flash="{maincolor:'pink',lastcolor:'silver',delay:500}">
      <div class="player-head">
        <h1> Rəqibin Canı</h1>
      </div>
      <div class="proqress" >
        <div class="bar" :style="{width:player+'%',backgroundColor: '#11ba'+(player-1)}"><b>{{player}}%</b></div>
      </div>
    </div>
  </div>
  <div class="body">
    <div class="body-btn" v-if="show">
      <button @click="show=!show" class="btn btn-1">Yeni Oyun</button>
    </div>
    <div class="body-btn"  v-if="!show">
      <button @click="user_attack" class="btn btn-1">Hücum</button>
      <button @click="help_attack" class="btn btn-2">Kömək</button>
      <button @click="special_attack" class="btn btn-3">Xüsusi Hücum</button>
      <button @click="iptal" class="btn btn-4">Məğlubiyyət!</button>
    </div>
  </div>
  <div class="footer" id="scroller" v-show="logs.length>1">
    <ul id="list">
      <li v-for="log in logs" :key='log' :class="{active:log.user=='U',passiv:log.player=='P'}">{{log.point}}</li>
    </ul>
  </div>
  <div>
    <!-- <filterlist-vuex></filterlist-vuex> -->
  </div>
</div>
</template>
<script>
      
// import filterlistVuex from "./filterList.vue"
import {data} from '../product'

export default {
  mixins:[data],
  components:{
    // filterlistVuex:filterlistVuex
  },
  directives:{
    "color":{
      bind(el,binding){
        var delay=0;
          if(binding.modifiers["delay"]){
            delay=2000;
          } 
          if(binding.modifiers["flash"]){
            let firstcolor=binding.value.maincolor;
            let secoundcolor=binding.value.lastcolor;
            let currentcolor=firstcolor;
            setTimeout(()=>{
              setInterval(()=>{
                currentcolor==firstcolor ?currentcolor=secoundcolor:currentcolor=firstcolor
                if(binding.arg=="background"){
                  el.style.backgroundColor=currentcolor;
              }
                else{
                  el.style.color=currentcolor;
                }
              },binding.value.delay)
            },delay)

          }
          else{
              setTimeout(()=>{
                if(binding.arg=="background"){
                  el.style.backgroundColor=binding.value;
              }
              else{
                el.style.color=binding.value;
              }
            },delay) ;
          }
      }
    }
  },
  data() {
    return{
      h1:'Mənim Canım',
      user:100,
      top:300,
      player:100,
      point:100,
      show:true,
      logs:[],
      searchText:""
    }
  },
  methods:{
    user_attack(){
      var point=Math.ceil(Math.random()*5);
      this.player=this.player-point;
      this.game_push({user:'U',point:"Mənim hücumum (" +point+ ")"});
      this.player_attack();
    },
    special_attack(){
      var point=Math.ceil(Math.random()*15);
      this.player=this.player-point;
      this.game_push({user:'U',point:"Mənim xüsusi hücumum (" +point+ ")"});
      this.player_attack();
    },
    player_attack(){
      var point=Math.ceil(Math.random()*10);
      this.user=this.user-point;
      this.game_push({player:'P',point:"Rəqibin hücumu (" +point+ ")"});
    },
    help_attack(){
      var point=Math.ceil(Math.random()*12);
      this.user=this.user+point;
      this.game_push({user:'U',point:"Mənim köməyim (" +point+ ")"});
      this.player_attack();
      if(this.user>=100){
        this.user=100
      }
    },
    iptal(){
      alert('Məğlub oldun !');
      this.show=true;
      this.user=100;
      this.player=100;
      this.logs=[];
    },
    game_push(value){
      this.logs.push(value)
    }
  },
  watch:{
      user(value){
        if(value<=10){
          this.iptal();
        }
      },
      player(value){
        if(value<=10){
          alert('Qalib oldun !')
          this.show=true;
          this.user=100;
          this.player=100;
          this.logs=[];
        }
      },
      logs(){
   
      }
  },
  filters:{
    tolowercase(value){
      return value.toLowerCase();
    }
  }
 
}
</script>

